import React, { Fragment, useEffect, useState } from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {
  Row,
  Col,
  Card,
  CardBody,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  FormFeedback,
} from 'reactstrap';

import './RouteAdd.scss';
import { Link, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import { addRoute } from '../../../store/actions/routeAction';

const RouteAdd = (props) => {
  const {
    addRoute,
    schoolId,
  } = props;
  const history = useHistory();
  const [loading, setLoading] = useState(false);

  const [routeName, setRouteName] = useState({
    value: '',
  });
  const [from, setFrom] = useState({
    value: '',
  });
  const [to, setTo] = useState({
    value: '',
  });

  // useEffect(() => {
  //   console.log("loading", loading)
  // }, [loading])

  const handleRouteNameChange = (e) => {
    e.preventDefault();
    setRouteName({
      ...routeName,
      value: e.target.value,
    });
  };
  const handleFromChange = (e) => {
    e.preventDefault();
    setFrom({
      ...from,
      value: e.target.value,
    });
  };
  const handleToChange = (e) => {
    e.preventDefault();
    setTo({
      ...to,
      value: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);

    const routeData = {
        schoolId: schoolId,
        routeName: routeName.value,
        from: from.value,
        to: to.value,
        deleted: false,
    };

    console.log('route data===', routeData);

    addRoute(
      routeData,
      () => {
        // setTimeout(() => {
        history.push('/routeManagement/list');
        // }, 2000)
      },
      () => setLoading(false)
    );
  };

  return (
    <Fragment>
      <ReactCSSTransitionGroup
        component='div'
        transitionName='TabsAnimation'
        transitionAppear={true}
        transitionAppearTimeout={0}
        transitionEnter={false}
        transitionLeave={false}
      >
        <Row>
          <Col md='12'>
            <Card className='main-card mb-3'>
              <CardBody>
                <div className='card-header-info'>
                  <div className='info'>
                    <div className='title'>Add Route Details</div>
                  </div>
                  <div className='action'>
                    <Link className='close-button' to='/routeManagement/list'>
                      <i className='lnr-cross-circle'> </i>
                    </Link>
                  </div>
                </div>
                <Form onSubmit={handleSubmit}>
                  <Row>                  
                    <Col md='12'>
                      <FormGroup>
                        <Label for='routeName'>Route Name</Label>
                        <Input
                          type='text'
                          name='routeName'
                          id='routeName'
                          value={routeName.value}
                          onChange={handleRouteNameChange}
                          placeholder='Enter Route Name'
                          required
                          maxLength="20"
                        />
                        <FormFeedback>
                          Route Name cannot be empty
                        </FormFeedback>
                      </FormGroup>
                    </Col>

                    <Col md='6'>
                      <FormGroup>
                        <Label for='from'>Route Origin</Label>
                        <Input
                          type='text'
                          name='from'
                          id='from'
                          value={from.value}
                          onChange={handleFromChange}
                          placeholder='Enter Route Origin'
                          required
                          maxLength="20"
                        />
                        <FormFeedback>
                          Route Origin cannot be empty
                        </FormFeedback>
                      </FormGroup>
                    </Col>

                    <Col md='6'>
                      <FormGroup>
                        <Label for='to'>Route Destination</Label>
                        <Input
                          type='text'
                          name='to'
                          id='to'
                          value={to.value}
                          onChange={handleToChange}
                          placeholder='Enter Route Destination'
                          required
                          maxLength="20"
                        />
                        <FormFeedback>
                          Route Destination cannot be empty
                        </FormFeedback>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Link className='close-button' to='/routeManagement/list'>
                    <Button color='secondary' className='mr-2 mt-1'>
                      Cancel
                    </Button>
                  </Link>
                  <Button color='primary' className='mt-1' disabled={loading}>
                    Submit
                  </Button>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </ReactCSSTransitionGroup>
    </Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    schoolId: state.auth.user.result.userExist._id,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addRoute: (routeData, navigate, stopLoader) =>
      dispatch(addRoute(routeData, navigate, stopLoader)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RouteAdd);
